/* ====================
    VARIABLES
==================== */
:root {
    --gutter: 15px;

    --color-dark: #000;
    --color-light: #fff;
    --color-gray-100: #bdbdbd;
    --color-gray-200: #828282;
    --color-primary: #2f80ed;
    --color-danger: #eb5757;
    --color-secondary: #333333;

    --font-family-primary: 'Raleway', sans-serif;
    --font-family-secondary: 'Montserrat', sans-serif;
}

/* ====================
    CONTAINER
==================== */
.App {
    width: 100vw;
}

.container {
    position: relative;
    width: 100%;
    max-width: 608px;
    padding-right: 15px;
    padding-right: var(--gutter);
    padding-left: 15px;
    padding-left: var(--gutter);
    margin-right: auto;
    margin-left: auto;
    text-align: center;
}

*,
*:before,
*:after {
    box-sizing: border-box;
    outline: none;
    padding: 0;
    margin: 0;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

body {
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    min-height: 100vh;
    padding: 2rem 0;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: var(--font-family-primary);
    font-style: normal;
    font-weight: bold;
    line-height: 1.25;
    letter-spacing: -0.045em;
    color: var(--color-secondary);
}

::-webkit-input-placeholder {
    font-family: var(--font-family-secondary);
    font-style: normal;
    font-size: 1rem;
    line-height: 1.1;
}

:-ms-input-placeholder {
    font-family: var(--font-family-secondary);
    font-style: normal;
    font-size: 1rem;
    line-height: 1.1;
}

::-ms-input-placeholder {
    font-family: var(--font-family-secondary);
    font-style: normal;
    font-size: 1rem;
    line-height: 1.1;
}

p,
li,
button,
input,
label,
::placeholder {
    font-family: var(--font-family-secondary);
    font-style: normal;
    font-size: 1rem;
    line-height: 1.1;
}

button {
    background-color: transparent;
    border: none;
    cursor: pointer;
}

